import { useContext, useEffect } from 'react'
import { observer } from 'mobx-react-lite'
import { Box } from '@mui/material'
import { RootStoreContext } from '../store/rootStoreContext'
import QuoteArea from './quoteArea'
import Slider from './slider'
import Attribution from './attribution'
import SuccessDisplay from './successDisplay'
import HintButton from './hintButton'

const GameBoard = (props) => {
  const [root] = useContext(RootStoreContext)

  // useEffect(() => {
  //   root.puzzleStore.loadRandomPuzzle()
  // }, [root.puzzleStore])

  const successMessage = (
    <Box>
      Nicely done, your score is {root.puzzleStore.current?.misses}!
      <br />
      Here's how you ranked with others on this puzzle:
    </Box>
  )

  return (
    <Slider>
      <Box sx={{ width: '95%', margin: '0 auto' }}>
        <QuoteArea id='quote' />
        <Box
          sx={{
            width: '100%',
          }}
        >
          <Attribution
            show={root.gameStore.puzzleState === 'succeeded'}
            html={root.gameStore.attributionHtml}
          />
          {root.gameStore.puzzleState === 'playing' && <HintButton />}
        </Box>

        {root.gameStore.puzzleState === 'succeeded' && (
          <Box sx={{ mt: 3 }}>
            <SuccessDisplay
              message={successMessage}
              fadeOutApplause={false}
              showNextButton={true}
              nextButtonAction={() => {
                root.puzzleStore.loadNextPuzzle()
              }}
            />
          </Box>
        )}
      </Box>
    </Slider>
  )
}

export default observer(GameBoard)
