import { useEffect, useState } from 'react'
import { Box, Fade, Typography } from '@mui/material'

const TutorialHelpPopup = (props) => {
  const { show = false, appearDelay = 500, message = '' } = props

  const [showMessageCore, setShowMessageCore] = useState(false)

  useEffect(() => {
    setTimeout(
      () => {
        setShowMessageCore(show)
      },
      show ? appearDelay : 0
    )
  }, [show, appearDelay])

  const boxStyle = {
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
  }
  const textStyle = {
    lineHeight: 1.2,
    textAlign: 'center',
  }

  return (
    <Box sx={boxStyle} id='helpText'>
      <Fade
        in={showMessageCore}
        timeout={{ enter: 500, exit: 50 }}
        sx={{ width: '80%' }}
      >
        <Box id='helpTextSub'>
          <Typography variant='h6' sx={textStyle}>
            {message}
          </Typography>
        </Box>
      </Fade>
    </Box>
  )
}

export default TutorialHelpPopup
