import { Box, Fade } from '@mui/material'

const Attribution = (props) => {
  const attributionStyle = {
    width: '100%',
    px: '5%',
    mt: '4px',
    lineHeight: 'normal',
    pointerEvents: props.show ? 'auto' : 'none',
    // don't want attribution to impact slider size calc when hidden
    // using display prop prevents transition from running
    fontSize: ['13pt', '14pt', '15pt'],
    textAlign: 'center',
  }

  return (
    <Fade in={props.show} timeout={{ enter: 1000, exit: 100 }}>
      <Box
        id='attribution'
        sx={attributionStyle}
        dangerouslySetInnerHTML={{ __html: props.html }}
      ></Box>
    </Fade>
  )
}

export default Attribution
