import { useContext, useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { Box, useTheme } from '@mui/material'
import { RootStoreContext } from '../../store/rootStoreContext'
import QuoteArea from '../quoteArea'
import Slider from '../slider'
import Attribution from '../attribution'
import SuccessDisplay from '../successDisplay'
import TutorialHelpPopup from '../tutorialHelpPopup'

const Tutorial = (props) => {
  const [root] = useContext(RootStoreContext)
  const theme = useTheme()

  useEffect(() => {
    root.gameStore.setBottomBarMode('singleLabel')
  }, [root.gameStore])

  useEffect(() => {
    root.puzzleStore.loadPuzzleById('tut1')
  }, [])

  const successMessages = [
    <Box sx={{}}>Well done!</Box>,
    <Box sx={{}}>Nice job, you're getting it.</Box>,
    <Box sx={{}}>
      Great work, you've got it figured out. <br /> <br />
      Now choose <b>daily</b> or <b>practice</b> below to keep playing.
    </Box>,
  ]

  const helpMessages = [
    <Box component='span'>
      The goal is to fix the order of the words. <br />
      <br />
      The two{' '}
      <Box
        component='span'
        sx={{ color: 'wordIncorrect.main', fontWeight: 800 }}
      >
        {theme.palette.incorrectWordColorName} words
      </Box>{' '}
      are out of place. <b>Tap</b> them to swap their order.
    </Box>,
    <Box component='span'>
      Keep swapping words <br />
      until all of them are{' '}
      <Box component='span' sx={{ color: 'wordCorrect.main', fontWeight: 800 }}>
        {theme.palette.correctWordColorName}
      </Box>
      .
    </Box>,
    <Box component='span'>
      Can you figure
      <br />
      this one out?
    </Box>,
  ]

  const tutPageIndex = () => {
    return tutPage.slice(-1) - 1
  }

  let tutPage = root.puzzleStore.current?.id

  if (tutPage)
    return (
      <Slider>
        <Box sx={{ position: 'relative' }}>
          <Box
            sx={{
              position: 'absolute',
              width: '100%',
              bottom: '50px',
            }}
          >
            <TutorialHelpPopup
              show={root.gameStore.puzzleState === 'playing'}
              message={helpMessages[tutPageIndex()]}
            />
          </Box>
        </Box>
        <QuoteArea showProgressBar={tutPage !== 'tut1'} />

        <Attribution
          show={root.gameStore.puzzleState === 'succeeded'}
          html={
            root.gameStore.puzzleState === 'succeeded'
              ? root.gameStore.attributionHtml
              : ''
          }
        />

        {root.gameStore.puzzleState === 'succeeded' && (
          <Box sx={{ mt: 8 }}>
            <SuccessDisplay
              showStats={false}
              showNextButton={tutPage !== 'tut3'}
              message={successMessages[tutPageIndex()]}
              fadeOutApplause={false}
              onApplauseEnter={() => {
                if (tutPage === 'tut3') {
                  root.gameStore.setBottomBarMode('doubleLabel')
                }
              }}
              nextButtonAction={() => {
                switch (tutPage) {
                  case 'tut1':
                    root.puzzleStore.loadPuzzleById('tut2')
                    break
                  case 'tut2':
                    root.puzzleStore.loadPuzzleById('tut3')
                    break
                  default:
                    break
                }
              }}
            />
          </Box>
        )}
      </Slider>
    )
}

export default observer(Tutorial)
