import { useContext, useState } from 'react'
import { Box, Button } from '@mui/material'
import { observer } from 'mobx-react-lite'
import { RootStoreContext } from '../store/rootStoreContext.js'
import GameBoard from './gameBoard'

const PracticePage = (props) => {
  const [root] = useContext(RootStoreContext)

  if (root.puzzleStore.chapter) {
    return <GameBoard />
  }

  const pageStyle = {
    display: 'flex',
    flexDirection: 'column',
    gap: 2,
    p: 3,
  }

  const selectChapter = (chapterId) => {
    root.puzzleStore.loadChapterById(chapterId)
    root.puzzleStore.loadNextPuzzle()
  }

  return (
    <Box>
      <Box id='practicePage' sx={pageStyle}>
        {root.puzzleStore.chapters.map((chap) => (
          <Button
            key={chap.id}
            variant='outlined'
            onClick={() => selectChapter(chap.id)}
          >
            {chap.difficultyLabel} ({chap.sequence.length})
          </Button>
        ))}
      </Box>
    </Box>
  )
}

export default observer(PracticePage)
