import { createTheme } from '@mui/material/styles'

export const getTheme = (uiTheme) => {
  const neutralPalette = {}

  const darkPalette = {
    primary: { main: '#dac8c0' },
    nearlyInvisible: { main: '#444' },
    secondary: { main: '#ac9f9b' },
    error: { main: '#FFA8A9' },
    link: { main: '#92b4f4' },
    success: { main: '#6aa84f' },
    wordCorrect: { main: '#6aa84f' },
    wordIncorrect: { main: '#ffa8a9' },
    incorrectWordColorName: 'pink',
    correctWordColorName: 'green',
    wordSelected: { main: '#ff7374' },
    mode: 'dark',
  }

  const lightPalette = {
    primary: { main: '#453F3C' },
    nearlyInvisible: { main: '#ccc' },
    secondary: { main: '#797270' },
    error: { main: '#FFA8A9' },
    link: { main: '#92b4f4' },
    success: { main: '#6aa84f' },
    wordCorrect: { main: '#6aa84f' },
    wordIncorrect: { main: '#e84b1a' },
    incorrectWordColorName: 'orange',
    correctWordColorName: 'green',
    wordSelected: { main: '#ff1a00' },
    mode: 'light',
  }

  const selectedPalette = uiTheme === 'dark' ? darkPalette : lightPalette

  return createTheme({
    palette: {
      ...neutralPalette,
      ...selectedPalette,
    },
    typography: {
      fontFamily: 'Nunito',
    },

    components: {
      MuiTypography: {
        styleOverrides: {
          root: {
            color: selectedPalette.primary.main,
          },
        },
      },
      MuiCssBaseline: {
        styleOverrides: {
          html: {
            margin: 0,
            height: '100%',
            // overflow: 'hidden',
          },
          body: {
            height: '100%',
          },
          '#root': {
            height: '100%',
          },
          '*': {
            //outline: '1px solid blue', // for debugging
          },
          a: {
            color: '#92b4f4', //todo - how can i refer to palette here?
          },
          'a, a:link, a:visited, a:hover, a:active': {
            textDecoration: 'none',
          },
        },
      },
    },
  })
}
