import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext'
import { Box, Typography } from '@mui/material'

const BannerAd = (props) => {
  const [root] = useContext(RootStoreContext)

  const adSpaceStyle = {
    position: 'fixed',
    top: '0',
    left: '0',
    height: `${root.gameStore.adSpaceHeight}px`,
    width: '100%',
    bgcolor: 'background.paper',
    borderColor: 'secondary.main',
    borderWidth: 1,
    borderStyle: 'dotted',
    p: 1,
    zIndex: 2,
  }

  return (
    <Box id='adspace' sx={adSpaceStyle}>
      <Typography color='nearlyInvisible.main'>
        v{root.gameStore.versionInfo}
      </Typography>
    </Box>
  )
}

export default observer(BannerAd)
