import { Box, Button, Badge } from '@mui/material'
import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import { RootStoreContext } from '../store/rootStoreContext.js'
import { EmojiObjectsOutlined } from '@mui/icons-material'

const HintButton = (props) => {
  const [root] = useContext(RootStoreContext)

  const boxStyle = {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    mt: 2,
  }

  const handleClick = () => {
    root.gameStore.setShowHintNow(true)
  }

  return (
    <Box id='hintButton' sx={{ ...boxStyle, ...props.sx }}>
      <Badge color='success' badgeContent={root.puzzleStore.current.hintsLeft}>
        <Button
          variant='outlined'
          onClick={handleClick}
          disabled={root.puzzleStore.current.hintsLeft === 0}
        >
          <EmojiObjectsOutlined />
        </Button>
      </Badge>
    </Box>
  )
}

export default observer(HintButton)
