import { makeAutoObservable } from 'mobx'

export class GameStore {
  rootStore
  puzzleState = ''
  instaSolve = false
  page = 'practice' // tutorial practice
  bottomBarMode = ''
  attributionHtml = ''
  adSpaceHeight = 60
  menuDrawerOpen = false
  doRevealSelectedWord = false
  showHintNow = false
  isWordSelected = false
  helpOpen = false
  uiTheme = 'system' // dark light system
  versionInfo = ''
  windowSize = [0, 0]

  constructor(rootStore) {
    this.rootStore = rootStore

    makeAutoObservable(this, {})
  }

  setUiTheme(theme) {
    if (!['system', 'dark', 'light'].includes(theme)) {
      throw new Error(`invalid state: ${theme}`)
    }
  }

  setVersionInfo(t) {
    this.versionInfo = t
  }

  setPuzzleState(state) {
    if (!['puzzleLoaded', 'playing', 'succeeded'].includes(state)) {
      throw new Error(`invalid state: ${state}`)
    }
    this.puzzleState = state
  }

  toggleInstaSolve(v) {
    this.instaSolve = !this.instaSolve
  }

  setAttributionHtml(v) {
    this.attributionHtml = v
  }

  setPage(v) {
    this.page = v
  }

  toggleDrawerOpen() {
    this.menuDrawerOpen = !this.menuDrawerOpen
  }

  setDoRevealSelectedWord(v) {
    this.doRevealSelectedWord = v
  }

  setShowHintNow(val) {
    this.showHintNow = val
  }

  setIsWordSelected(v) {
    this.isWordSelected = v
  }

  setHelpOpen(v) {
    this.helpOpen = v
  }

  setBottomBarMode(v) {
    this.bottomBarMode = v
  }

  get inTutorial() {
    return this.page === 'tutorial'
  }

  setWindowSize(size) {
    this.windowSize = size
  }
}
