import React, { useState, useEffect, useContext } from 'react'
import { observer } from 'mobx-react-lite'
import ReactGA from 'react-ga4'
import { Box, Container } from '@mui/material'
import CssBaseline from '@mui/material/CssBaseline'
import useMediaQuery from '@mui/material/useMediaQuery'
import { ThemeProvider } from '@mui/material/styles'
import { getTheme } from './theme'
import { RootStoreContext } from './store/rootStoreContext'
import HelpDialog from './components/helpDialog'
import SettingsDialog from './components/settingsDialog'
import BannerAd from './components/bannerAd'
import Tutorial from './components/tutorial/tutorial'
import BottomBar from './components/bottomBar'
import { Case, Switch } from './components/helper/switch'
import { useResize } from './components/helper/useResize'
import PracticePage from './components/practicePage'
import { logEvent } from './service/analytics'

const DailyPage = () => {
  return <Box>Daily</Box>
}

const App = () => {
  const [root] = useContext(RootStoreContext)
  const [settingsOpen, setSettingsOpen] = useState(false) //todo: move to store
  // eslint-disable-next-line no-unused-vars
  let windowSize = useResize() // causes re-render when window size changes

  useEffect(() => {
    root.gameStore.setWindowSize(windowSize)
  }, [windowSize, root.gameStore])

  useEffect(() => {
    logEvent('app_started')
  }, [])

  useEffect(() => {
    async function getVersion() {
      await fetch('version.txt')
        .then((r) => r.text())
        .then((text) => {
          root.gameStore.setVersionInfo(text.substring(0, 30))
        })
    }
    getVersion()
  }, [root.gameStore])

  let theme
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)')
  if (root.gameStore.uiTheme === 'system') {
    theme = getTheme(prefersDarkMode ? 'dark' : 'light')
  } else {
    theme = getTheme(root.gameStore.uiTheme)
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth='sm' id='container' sx={{ px: 0 }}>
        <BannerAd />
        <Box
          id='top-layout'
          sx={{
            position: 'relative',
            height: `calc(100dvh - ${root.gameStore.adSpaceHeight}px)`,
            mt: `${root.gameStore.adSpaceHeight}px`,
            px: 2,
            contain: 'content',
          }}
        >
          <Box
            id='aqua'
            sx={{
              display: 'flex',
              height: `calc(100% - 45px)`,
            }}
          >
            <Switch test={root.gameStore.page}>
              <Case value='tutorial'>
                <Tutorial />
              </Case>
              <Case value='daily'>
                <DailyPage />
              </Case>
              <Case value='practice'>
                <PracticePage />
              </Case>
            </Switch>
          </Box>
          <SettingsDialog
            open={settingsOpen}
            closeHandler={() => setSettingsOpen(false)}
          />

          <HelpDialog
            open={root.gameStore.helpOpen}
            closeHandler={() => root.gameStore.setHelpOpen(false)}
          />
        </Box>
        <BottomBar />
      </Container>
    </ThemeProvider>
  )
}

export default observer(App)
