import { useContext, useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { Box, Fade } from '@mui/material'
import {
  startGamePlay,
  selectWord,
  revealSelectedWord,
  showNextHint,
  refreshTextPresentation,
} from './gameplay/formatting'
import { RootStoreContext } from '../store/rootStoreContext'
import { puzzleWordStyles } from './quoteAreaStyle'
import ProgressBar from './progressBar'
import MissesBar from './missesBar'

let pointerDownElems = []

const setupEventHandlers = () => {
  const pointerDownOnPuzzleWords = (ev) => {
    ev.preventDefault() // trying to prevent double tap to zoom
    const downElem = ev.srcElement
    pointerDownElems.push(downElem)
  }
  const pointerUpOnPuzzleWords = (ev) => {
    const upElem = ev.srcElement
    if (pointerDownElems.includes(upElem)) {
      selectWord(ev.x, ev.y, upElem)
      // remove it from list
      pointerDownElems = pointerDownElems.filter((el) => upElem !== el)
    }
  }

  const span = document.querySelector('#sentence')
  span.addEventListener('pointerdown', pointerDownOnPuzzleWords)
  span.addEventListener('pointerup', pointerUpOnPuzzleWords)

  document.addEventListener('pointerdown', (ev) => {
    if (!ev.target.closest('#sentence') && ev.target.type !== 'button') {
      selectWord(null)
      pointerDownElems = []
    }
  })
}

const QuoteArea = (props) => {
  const { showProgressBar = true } = props
  const [root] = useContext(RootStoreContext)
  const [showSentence, setShowSentence] = useState(false)
  const fadeDelay = 250

  useEffect(() => {
    setupEventHandlers()
  }, [])

  useEffect(() => {
    async function initPuzzle() {
      await startGamePlay(root)
      setShowSentence(true)
      root.gameStore.setPuzzleState('playing')
      //sendEvent('gameboard', 'new_puzzle')
    }

    if (root.gameStore.puzzleState === 'puzzleLoaded') {
      setShowSentence(false)
      setTimeout(() => initPuzzle(), fadeDelay)
    }
  }, [root, root.gameStore.puzzleState])

  useEffect(() => {
    if (root.gameStore.doRevealSelectedWord) {
      revealSelectedWord()
      root.gameStore.setDoRevealSelectedWord(false)
    }
  }, [root.gameStore, root.gameStore.doRevealSelectedWord])

  useEffect(() => {
    if (root.gameStore.showHintNow && root.puzzleStore.current.hintsLeft > 0) {
      root.gameStore.setShowHintNow(false)
      showNextHint()
    }
  }, [
    root.gameStore,
    root.gameStore.showNextHint,
    root.puzzleStore,
    root.gameStore.showHintNow,
  ])

  useEffect(() => {
    if (root.gameStore.puzzleState === 'playing') {
      refreshTextPresentation()
    }
  }, [root.gameStore.puzzleState, root.gameStore.windowSize])

  const sentenceStyle = {
    fontSize: ['22pt', '24pt', '26pt'],
    border: '2px solid #79727044',
    borderRadius: '10px',
    width: '100%',
    py: [1, 2, 2],
    pr: [1, 2, 2],
    pl: [2, 3, 3],
    lineHeight: [1.5, 1.3, 1.2],
    userSelect: 'none',
    WebkitUserSelect: 'none',
    msUserSelect: 'none',
    WebkitTouchCallout: 'none',
    WebkitTextSizeAdjust: 'none',
    touchAction: 'manipulation' /* blocks double tap zoom */,
    fontFamily: 'Roboto',
    textAlign: 'center',
  }

  const puzzleHudStyle = {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
    gap: '5px',
    flexWrap: 'nowrap',
    pr: '10px',
  }

  return (
    <Fade in={showSentence} appear={true} timeout={fadeDelay}>
      <Box>
        <Box id='hudTop' sx={puzzleHudStyle}>
          {showProgressBar && <MissesBar id='scoreBar' />}
          {showProgressBar && <ProgressBar id='scoreBar' />}
        </Box>
        <Box id='sentence' sx={sentenceStyle}>
          <Box component='span' id='draggable' sx={puzzleWordStyles}></Box>
        </Box>
      </Box>
    </Fade>
  )
}

export default observer(QuoteArea)
