import { useState } from 'react'
import { Box, Typography, Button, Fade } from '@mui/material'
import Applause from './applause'
import ScoreReport from './scoreReport'
import Delayed from './delayed'

const SuccessDisplay = (props) => {
  const {
    applauseDuration = 2000,
    showStats = true,
    message = '',
    onApplauseEnter = () => {},
    onApplauseComplete = () => {},
    nextButtonAction = () => {},
    fadeOutApplause = true,
    showNextButton = false,
  } = props

  const [applause, setApplause] = useState(true)

  const NextButton = (props) => {
    return (
      <Button variant='outlined' sx={{ mt: 2 }} onClick={nextButtonAction}>
        next
      </Button>
    )
  }

  const textStyle = {
    px: '20px',
    lineHeight: [1, 1.2, 1.5],
  }

  return (
    <Box sx={{ mt: 2, width: '100%', textAlign: 'center' }}>
      <Delayed waitBeforeShow={750}>
        <Fade
          in={applause}
          timeout={{ enter: 900, exit: 100 }}
          exit={false}
          onEntered={(ev) => {
            setTimeout(() => {
              if (fadeOutApplause) {
                setApplause(false)
              }
            }, applauseDuration)
            onApplauseEnter()
          }}
          onExited={(ev) => {
            ev.style.display = fadeOutApplause ? 'none' : 'block'
            onApplauseComplete()
          }}
        >
          <Box>
            <Applause />
            <Typography variant='h5' sx={textStyle}>
              {message}
            </Typography>
            {showStats && <ScoreReport />}
            {showNextButton && <NextButton />}
          </Box>
        </Fade>
      </Delayed>
      <br />
      <br />
    </Box>
  )
}

export default SuccessDisplay
