import { Box } from '@mui/system'
import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext.js'

const MissesBar = (props) => {
  const [root] = useContext(RootStoreContext)

  const MissBox = () => {
    const style = {
      display: 'inline-block',
      color: 'wordIncorrect.main',
      background: 'currentColor', //omg it worked
      opacity: 0.7,
      height: '6px',
      width: '10px',
      ml: '5px',
      verticalAlign: 'top',
    }
    return <Box component='span' id='box' sx={style}></Box>
  }

  const len = Math.min(10, root.puzzleStore.current?.misses)

  return (
    <Box id='missesBox' sx={{ height: '10px' }}>
      {Array.from({ length: len }).map((_, i) => (
        <MissBox key={i} />
      ))}
    </Box>
  )
}

export default observer(MissesBar)
