import { makeAutoObservable } from 'mobx'
import { Puzzle } from './Puzzle'
import puzzleJson from '../assets/puzzles/puzzleData.json'
import chapterJson from '../assets/puzzles/chapterData.json'
import { Chapter } from './chapter'

export class PuzzleStore {
  rootStore = null // is this really needed?
  current = null
  puzzles = []
  usedIds = []
  chapter = null
  chapters = []
  cursorInChapter // move to chapter.js?

  constructor(rootStore) {
    this.rootStore = rootStore

    makeAutoObservable(this, {
      usedIds: false,
    })

    for (const chapData of chapterJson) {
      this.addChapter(new Chapter(chapData))
    }
  }

  addChapter(c) {
    this.chapters.push(c)
  }

  loadChapterById(id) {
    this.chapter = this.chapters.find((c) => c.id === id)
    this.cursorInChapter = 0
  }

  loadPuzzleById(id) {
    const puzzles = puzzleJson
    const data = puzzles.find((p) => p.id === String(id))
    this.current = new Puzzle(data)
    this.rootStore.gameStore.setPuzzleState('puzzleLoaded')
  }

  loadNextPuzzle() {
    if (this.chapter) {
      if (this.cursorInChapter < this.chapter.sequence.length) {
        this.loadPuzzleById(this.chapter.sequence[this.cursorInChapter])
        this.cursorInChapter += 1
      } else {
        this.chapter = null
      }
    } else {
      this.loadRandomPuzzle()
    }
  }

  loadRandomPuzzle() {
    this.loadPuzzleById(124)
    return
    const puzzles = puzzleJson.filter((p) => !p['id'].includes('tut'))
    let id = 0
    do {
      id = puzzles[Math.floor(Math.random() * puzzles.length)]['id']
    } while (this.usedIds.includes(id))
    this.usedIds.push(id)
    this.loadPuzzleById(id)
  }
}
