import { useContext } from 'react'
import { observer } from 'mobx-react-lite'
import * as React from 'react'
import Menu from '@mui/material/Menu'
import MenuItem from '@mui/material/MenuItem'
import { Box, IconButton, ListItemIcon, ListItemText } from '@mui/material'
import {
  Menu as MenuIcon,
  Waves as WaveIcon,
  SkipNext,
} from '@mui/icons-material'
import SendIcon from '@mui/icons-material/Send'
import RefreshIcon from '@mui/icons-material/Refresh'
import { RootStoreContext } from '../store/rootStoreContext.js'
import MovesBar from './healthBar'

const BasicMenu = (props) => {
  const [root] = useContext(RootStoreContext)
  const [anchorEl, setAnchorEl] = React.useState(null)
  const open = Boolean(anchorEl)
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }
  const handleClose = () => {
    setAnchorEl(null)
  }

  const menuStyle = {
    width: '15%',
    minWidth: '250px',
  }

  const listItems = [
    {
      text: 'Next puzzle',
      icon: <RefreshIcon />,
      handler: () => root.puzzleStore.loadNextPuzzle(),
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Goto puzzle',
      icon: <SendIcon />,
      handler: () => {
        const id = prompt('Puzzle id?', '')
        root.puzzleStore.loadPuzzleById(id)
      },
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Run tutorial',
      icon: <WaveIcon />,
      handler: () => {
        root.gameStore.setPage('tutorial')
      },
      enabled: !root.gameStore.inTutorial,
    },
    {
      text: 'Toggle InstaSolve',
      icon: <SkipNext />,
      handler: () => root.gameStore.toggleInstaSolve(),
      enabled: true,
    },
  ]

  return (
    <Box sx={{ ...menuStyle, ...props.sx }}>
      <IconButton
        color='inherit'
        aria-label='open menu'
        onClick={handleClick}
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup='true'
        aria-expanded={open ? 'true' : undefined}
      >
        <MenuIcon />
      </IconButton>

      <Menu
        id='basic-menu'
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: -200, horizontal: 'right' }}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {listItems.map((item) => (
          <MenuItem
            key={item.text}
            onClick={() => {
              item.handler()
              handleClose()
            }}
          >
            <ListItemIcon>{item.icon} </ListItemIcon>
            <ListItemText>{item.text}</ListItemText>
          </MenuItem>
        ))}
        <MovesBar sx={{ pl: '4px', lineHeight: '30px' }} />

        <Box sx={{ pl: '20px' }}>puzzle id: {root.puzzleStore.current?.id}</Box>
      </Menu>
    </Box>
  )
}

export default observer(BasicMenu)
