import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext'
import { Box, Button, Typography } from '@mui/material'
import Menu from './menu'
import HelpPopup from './helpPopup'

const BottomBar = (props) => {
  const [root] = useContext(RootStoreContext)

  const outerBarStyle = {
    position: 'fixed',
    bottom: 0,
    left: 0,
    right: 0,
    height: '45px',
    width: '100%',
    bgcolor: 'background.paper',
    zIndex: 2,
  }
  const innerBarStyle = {
    position: 'relative',
    fontSize: '12pt',
    borderTop: '1px solid gray',
    width: '600px',
    maxWidth: '100vw',
    left: '50%',
    transform: 'translateX(-50%)',
  }

  const singleLabelStyle = {
    textAlign: 'center',
    mt: 1,
    width: '100%',
  }

  const dailyLabelStyle = {
    fontWeight: root.gameStore.page === 'daily' ? 800 : 400,
    justifyContent: 'flex-end',
  }

  const centerPipeStyle = {
    mx: 1,
  }

  const practiceLabelStyle = {
    fontWeight: root.gameStore.page === 'practice' ? 800 : 400,
    justifyContent: 'flex-start',
  }

  const menuPopupStyle = {
    position: 'absolute',
    left: '5px',
  }

  const helpPopupStyle = {
    position: 'absolute',
    right: '5px',
  }

  const NavLink = (props) => {
    return (
      <Button
        underline='hover'
        sx={{
          height: '10px',
          textDecoration: 'none',
          textTransform: 'none',
          ...props.sx,
        }}
        onClick={() => root.gameStore.setPage(props.page)}
      >
        {props.children}
      </Button>
    )
  }
  return (
    <Box id='bottomBar' sx={outerBarStyle}>
      <Box sx={innerBarStyle}>
        {root.gameStore.tutPage}
        {root.gameStore.bottomBarMode === 'singleLabel' ? (
          <Box sx={singleLabelStyle}>tutorial</Box>
        ) : (
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'center',
            }}
          >
            <Menu sx={menuPopupStyle} />
            <Box sx={{ mt: '7px', zIndex: '2' }}>
              <NavLink sx={dailyLabelStyle} page='daily'>
                daily
              </NavLink>
              <Typography component='span' sx={centerPipeStyle}>
                |
              </Typography>
              <NavLink sx={practiceLabelStyle} page='practice'>
                practice
              </NavLink>
            </Box>
            <HelpPopup sx={helpPopupStyle} />
          </Box>
        )}
      </Box>
    </Box>
  )
}

export default observer(BottomBar)
