import { isPunctuation, getRandomInt } from '../../lib/utils'
import { logEvent, trace, key } from '../../service/analytics'

let container = null // actual dom parent (#draggable)
let flatWordElems = [] // collection of elements of just the word spans, in whatever order
let selectableWordRects = {}
let wordsToSwap = []
let isSwapping = false
let firstSelectedWord = null
let isGameActive = false
let prevCorrectWordCount = -1
let root = null
let startTime
// todo: rename this file

async function startGamePlay(rootStore) {
  root = rootStore
  buildDomTree(root.puzzleStore.current.wordList.slice())
  root.gameStore.setAttributionHtml(root.puzzleStore.current.attribution) //todo: why is formatting.js the one doing this?
  prevCorrectWordCount = root.puzzleStore.current.getCountOfCorrectWords(
    flatWordElems.map((e) => e.textContent)
  )

  refreshTextPresentation()
  // delaying this to be sure the slider transition has completed first
  setTimeout(cacheSelectableWordPositions, 1100)
  isGameActive = true
  logEvent('puzzle_started', { puzzle_id: root.puzzleStore.current.id })
  startTime = Date.now()
}

function buildDomTree(words) {
  // due to linebreaking behavior for inline-block elements
  // (which I'm using in order to enable the word swap visuals)
  // the tree structure is funky. When there is punctuation,
  // the previous word and the punctuation character(s) are
  // parented to an intermediate span with 'nowrap' style.
  // these wrapper spans are siblings to the regular word spans.

  // if/when i implement my own line layout/positioning, this
  // weird structure would no longer be necessary as i would
  // have custom line breaking.
  container = document.getElementById('draggable')
  container.replaceChildren()
  for (let i = words.length - 1; i >= 0; i--) {
    let txtContent = words[i]
    let noWrapSpan = null
    if (isPunctuation(txtContent)) {
      noWrapSpan = document.createElement('span')
      noWrapSpan.classList.add('nowrap')
      while (isPunctuation(txtContent)) {
        const puncSpan = document.createElement('span')
        puncSpan.classList.add('punctuation')
        puncSpan.appendChild(document.createTextNode(txtContent))
        noWrapSpan.appendChild(puncSpan)
        i--
        txtContent = words[i]
      }
    }

    const wordSpan = document.createElement('span')
    wordSpan['originalIndex'] = i
    wordSpan.classList.add('word')
    wordSpan.prepend(document.createTextNode(txtContent))

    if (noWrapSpan) {
      noWrapSpan.prepend(wordSpan)
      container.prepend(noWrapSpan)
    } else {
      container.prepend(wordSpan)
    }
  }

  updateFlatElemList()
}

function updateFlatElemList() {
  flatWordElems = getDescendantElements(container).filter(
    (e) => !e.classList.contains('nowrap')
  )
}

function refreshTextPresentation() {
  setCorrectSpaces()
  restoreCapitalization()
  setWordFormatting()
  setTimeout(cacheSelectableWordPositions, 10)
}

function setCorrectSpaces() {
  for (let el of flatWordElems) {
    el.style.marginRight = '0px'
  }

  const spaceWidth = 10
  for (let el of container.children) {
    if (el.classList.contains('nowrap') || el.classList.contains('word')) {
      el.style.marginRight = `${spaceWidth}px`
    }
  }
}

function restoreCapitalization() {
  // if word is first word or after a period, or is capitlized in likelyProperNameMap
  // make capital, else don't.
  for (let i = 0; i < flatWordElems.length; i++) {
    const el = flatWordElems[i]
    const text = el.textContent?.trim().toLowerCase()
    const isFirst = i === 0
    const needsPostPuncCapitalization =
      flatWordElems[i - 1]?.classList.contains('punctuation') &&
      flatWordElems[i - 1].textContent.match(/[!?.]$/)
    const isProperNoun = root.puzzleStore.current.isProperNoun(text)
    const capitalizeIt = isFirst || needsPostPuncCapitalization || isProperNoun
    let firstLetter = text[0]
    firstLetter = capitalizeIt
      ? firstLetter.toUpperCase()
      : firstLetter.toLowerCase()
    el.textContent = firstLetter + text.slice(1)
  }
}

function setWordFormatting() {
  let prevIsCorrect = false
  for (let i = 0; i < flatWordElems.length; i++) {
    const child = flatWordElems[i]

    let isCorrect = false
    if (i > 0 && child.classList.contains('punctuation')) {
      isCorrect = prevIsCorrect // adopt formatting of previous word
    } else {
      const wordIndex = flatWordElems.indexOf(child)
      const spanText = child.textContent
      isCorrect = root.puzzleStore.current.isAInCorrectLocation(
        spanText,
        wordIndex
      )
    }

    child.classList.remove(
      'correct',
      'incorrect',
      'selected',
      'incorrectUnderline'
    )
    child.classList.add(isCorrect ? 'correct' : 'incorrect')
    if (!isCorrect && !child.classList.contains('punctuation')) {
      // we don't want to underline the punctuation
      child.classList.add('incorrectUnderline')
    }
    prevIsCorrect = isCorrect
  }
}

function swapWords(a, b) {
  if (a === b) return
  isSwapping = true
  if (flatWordElems.indexOf(a) > flatWordElems.indexOf(b)) {
    // hack: if b is before a in the dom, after the
    // transition, b re-animates right to left
    const tmp = a
    a = b
    b = tmp
  }

  wordsToSwap = [a, b]
  const posA = getPositionRelativeToContainer(a)
  const posB = getPositionRelativeToContainer(b)

  let transLeftA = posB.centerX - posA.centerX
  let transTopA = posB.centerY - posA.centerY
  let transLeftB = posA.centerX - posB.centerX
  let transTopB = posA.centerY - posB.centerY

  if (posA.top === posB.top) {
    const shiftAmount = (posB.width - posA.width) / 2
    transLeftA += shiftAmount
    transLeftB += shiftAmount
  } else {
    transLeftA += (posA.width - posB.width) / 2
    transLeftB += (posB.width - posA.width) / 2
  }

  a.style.setProperty('--transLeft', `${transLeftA}px`)
  a.style.setProperty('--transTop', `${transTopA}px`)
  b.style.setProperty('--transLeft', `${transLeftB}px`)
  b.style.setProperty('--transTop', `${transTopB}px`)

  oneTimeListener(a, 'transitionend', endSwapTransition)
  a.classList.add('wordSwap')
  b.classList.add('wordSwap')
}

function endSwapTransition(ev) {
  const a = wordsToSwap[0]
  const b = wordsToSwap[1]
  a.classList.remove('wordSwap')
  b.classList.remove('wordSwap')
  a.style.removeProperty('--transLeft')
  a.style.removeProperty('--transTop')
  swapDomElements(a, b)
  updateFlatElemList()
  checkForCompletion()
  refreshTextPresentation()
  isSwapping = false
}

function getPositionRelativeToContainer(el) {
  const parentPos = container.getBoundingClientRect()
  const childPos = el.getBoundingClientRect()
  const relPos = {}

  relPos.top = childPos.top - parentPos.top
  relPos.right = childPos.right - parentPos.right
  relPos.bottom = childPos.bottom - parentPos.bottom
  relPos.left = childPos.left - parentPos.left

  relPos.centerX = childPos.x + childPos.width / 2
  relPos.centerY = childPos.y + childPos.height / 2

  relPos.width = childPos.width
  relPos.height = childPos.height

  return relPos
}

function swapDomElements(obj1, obj2) {
  const parent2 = obj2.parentNode
  const next2 = obj2.nextSibling
  if (next2 === obj1) {
    parent2.insertBefore(obj1, obj2)
  } else {
    obj1.parentNode.insertBefore(obj2, obj1)
    if (next2) {
      parent2.insertBefore(obj1, next2)
    } else {
      parent2.appendChild(obj1)
    }
  }
}

function cacheSelectableWordPositions() {
  selectableWordRects = {}
  for (let i = 0; i < flatWordElems.length; i++) {
    const el = flatWordElems[i]
    if (el.classList.contains('word') && el.classList.contains('incorrect')) {
      selectableWordRects[i] = el.getBoundingClientRect()
    }
  }
}

function findClosestSelectableWordToPoint(point) {
  let closestIndex = -1
  let closestDistance = 1000000
  for (const rectIndex in selectableWordRects) {
    const rectDist = getRectDistance(point, selectableWordRects[rectIndex])
    if (rectDist < closestDistance && rectDist < 25) {
      closestDistance = rectDist
      closestIndex = rectIndex
    }
  }

  return closestIndex === -1 ? null : flatWordElems[closestIndex]
}

function selectWord(x, y) {
  if (isSwapping) {
    return
  }

  const el = findClosestSelectableWordToPoint({ x: x, y: y })
  if (!el || el === firstSelectedWord) {
    firstSelectedWord = null
    root.gameStore.setIsWordSelected(false)
    refreshTextPresentation()
    return
  }
  if (!(el.classList.contains('word') && el.classList.contains('incorrect'))) {
    return
  }
  if (!firstSelectedWord) {
    el.classList.add('selected')
    firstSelectedWord = el
    root.gameStore.setIsWordSelected(true)
  } else {
    el.classList.add('selected')
    swapWords(firstSelectedWord, el)
    firstSelectedWord = null
    root.gameStore.setIsWordSelected(false)
  }
}

function showNextHint() {
  const originalHintIndex = root.puzzleStore.current.getNextHint()
  let swapPartner = flatWordElems[originalHintIndex]

  // get the text of the hint and find the first non green instance of it.
  // swap that with what's currently in it's correct place
  const hintWordText =
    root.puzzleStore.current.originalWordList[originalHintIndex].toLowerCase()
  let hintElement = flatWordElems.find((el, i) => {
    const elText = el.textContent.toLowerCase()
    return (
      hintWordText === elText &&
      elText !== root.puzzleStore.current.originalWordList[i]
    )
  })

  if (!hintElement) {
    // All instances of the hint word have been used,
    // so use a random non-green word instead
    const incorrectElements = flatWordElems.filter(
      (el, i) =>
        !root.puzzleStore.current.isAInCorrectLocation(el.textContent, i) &&
        !isPunctuation(el.textContent)
    )
    hintElement = incorrectElements[getRandomInt(0, incorrectElements.length)]

    // find the first non-green spot that originally held the text
    // of hintElement
    swapPartner = flatWordElems.find(
      (el, i) =>
        !root.puzzleStore.current.isAInCorrectLocation(el.textContent, i) &&
        root.puzzleStore.current.originalWordList[i] ===
          hintElement.textContent.toLowerCase()
    )
  }

  swapWords(hintElement, swapPartner)
}

function revealSelectedWord() {
  if (root.puzzleStore.current.hintsLeft > 0) {
    let swapIndex = -1
    const selectedWordText = firstSelectedWord.textContent
    if (true) {
      const candIndices =
        root.puzzleStore.current.getOriginalIndicesForWord(selectedWordText)
      // find the first non-used spot where the same word is found
      swapIndex = candIndices.find(
        (ci) =>
          flatWordElems[ci].textContent.toLowerCase() !==
          selectedWordText.toLowerCase()
      )
    } else {
      // find the word that belongs at this location and bring it here
      swapIndex = root.puzzleStore.current.getIndexForMatchingWord(
        firstSelectedWord.originalIndex
      )
    }

    swapWords(firstSelectedWord, flatWordElems[swapIndex])
    firstSelectedWord = null
    root.gameStore.setIsWordSelected(false)
    root.puzzleStore.current.decrementHintsLeft()
  }
}

function checkForCompletion() {
  if (!isGameActive) return

  const correctWordCount = root.puzzleStore.current.getCountOfCorrectWords(
    flatWordElems.map((e) => e.textContent)
  )

  if (correctWordCount === prevCorrectWordCount) {
    wordsToSwap.forEach((e) => {
      const className = 'flashOnIncorrect'
      e.classList.add(className)
      oneTimeListener(e, 'animationend', (ev) => {
        ev.target.classList.remove(className)
      })
    })
  }

  if (correctWordCount === prevCorrectWordCount) {
    root.puzzleStore.current.registerMiss()
  } else {
    root.puzzleStore.current.registerGreenMoves(
      correctWordCount - prevCorrectWordCount
    )
  }

  wordsToSwap = []

  prevCorrectWordCount = correctWordCount
  const solved = correctWordCount === flatWordElems.length
  if (root.gameStore.instaSolve || solved) {
    isGameActive = false

    const elapsedSeconds = (Date.now() - startTime) / 1000
    const params = {
      puzzle_id: root.puzzleStore.current.id,
      chapter_id: root.puzzleStore.chapter.id,
      duration_sec: elapsedSeconds,
      hints_used: root.puzzleStore.current.hintsUsed,
      misses: root.puzzleStore.current.misses,
    }

    logEvent('puzzle_completed', params)
    trace(key.puzzle_completed, params)

    setTimeout(() => {
      root.gameStore.setPuzzleState('succeeded')
    }, 300)
  }
}

function oneTimeListener(node, type, callback) {
  node.addEventListener(
    type,
    function listener(e) {
      e.target.removeEventListener(e.type, listener)
      return callback.call(this, e)
    },
    { once: true }
  )
}

function getDescendantElements(elem, all = []) {
  for (let child of elem.children) {
    all.push(child)
    getDescendantElements(child, all)
  }

  return all
}

function clamp(value, min, max) {
  return Math.max(min, Math.min(value, max))
}

export function getRectDistance(point, rect) {
  const left = rect.x
  const top = rect.y
  const right = rect.x + rect.width
  const bottom = rect.y + rect.height

  const nearestX = clamp(point.x, left, right)
  const nearestY = clamp(point.y, top, bottom)

  const dx = point.x - nearestX
  const dy = point.y - nearestY

  return Math.sqrt(dx * dx + dy * dy)
}

export {
  startGamePlay,
  selectWord,
  revealSelectedWord,
  showNextHint,
  refreshTextPresentation,
}
