import { Box } from '@mui/material'
import { useEffect, useState, useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext'
import { observer } from 'mobx-react-lite'

const Slider = (props) => {
  const [root] = useContext(RootStoreContext)
  const [shiftYAmount, setShiftYAmount] = useState(0)
  const defaultShiftYAmount = 30

  useEffect(() => {
    if (root.gameStore.puzzleState === 'playing') {
      const sentenceHeight = document.getElementById('sentence').offsetHeight
      const halfParentHeight =
        document.getElementById('top-layout').offsetHeight * 0.5 - 32
      setShiftYAmount(halfParentHeight - 0.5 * sentenceHeight)
    } else {
      setShiftYAmount(defaultShiftYAmount)
    }
  }, [
    defaultShiftYAmount,
    root.gameStore.windowSize,
    root.gameStore.puzzleState,
  ])

  const sliderStyle = {
    width: ['98%', '95%', '90%'],
    alignItems: 'center',
    margin: '0 auto',
    overflow: 'auto',
    display: root.gameStore.puzzleState !== 'puzzleLoaded' ? 'block' : 'none',
    overscrollBehavior: 'contain',
    '&::-webkit-scrollbar': {
      display: 'none',
      width: '0.512rem',
    },
  }

  const innerBoxStyle = {
    position: 'relative',
    transition:
      root.gameStore.puzzleState !== 'succeeded'
        ? ''
        : 'margin-top 1000ms ease-in-out',
    marginTop: `${shiftYAmount}px`,
  }

  return (
    <Box id='slider' alignItems='center' spacing={0} sx={sliderStyle}>
      <Box sx={innerBoxStyle}>{props.children}</Box>
    </Box>
  )
}

export default observer(Slider)
