import { makeAutoObservable } from 'mobx'
import { isPunctuation } from '../lib/utils'

export class Puzzle {
  attribution
  id
  lang
  positions
  propnouns
  tags
  text
  ver
  wordList
  originalWordList
  category = ''
  hints = []
  hintsUsed = 0
  difficulty

  constructor(obj) {
    this.setPuzzleData(obj)
    this.updateHyperlinks()
    this.generateWordList()
    this.savePropNounsAsStrings()

    makeAutoObservable(this, {
      isAInCorrectLocation: false,
      getCountOfCorrectWords: false,
      getWordIndexMatchingWord: false,
    })
  }

  setPuzzleData(obj) {
    this.attribution = obj.attribution
    this.id = obj.id
    this.lang = obj.lang
    this.positions = obj.positions
    this.propnouns = obj.propnouns
    this.tags = obj.tags
    this.text = obj.text
    this.ver = obj.ver
    this.hints = obj.hints || []
    this.difficulty = obj.difficulty || -1
  }

  updateHyperlinks() {
    this.attribution = this.attribution.replace(
      '<a href=',
      "<a target='_blank' href="
    )
  }

  generateWordList() {
    const puncRegex = /[.;!:?,]+/g
    const txtWithSpaces = this.text.replace(puncRegex, ' $&')
    this.wordList = txtWithSpaces.split(' ')

    this.originalWordList = [...this.wordList].map((x) => x.toLowerCase())

    const positionedList = []
    for (let iPos = 0; iPos < this.positions.length; iPos++) {
      positionedList.push(this.wordList[this.positions[iPos]])
    }

    this.wordList = positionedList

    const totalWordCountIncludingPunctuation = this.wordList.length
    this.puncCount = this.wordList.filter((w) => isPunctuation(w)).length
    const givensCount =
      this.getCountOfCorrectWords(this.wordList) - this.puncCount
    this.totalPossibleGreenMoves =
      totalWordCountIncludingPunctuation - this.puncCount
    this.greenMoves = givensCount
    this.totalWordsMinusPunctuation =
      totalWordCountIncludingPunctuation - this.puncCount
    this.shrinkage = 0
  }

  savePropNounsAsStrings() {
    this.propnounStrings = []
    for (let i of this.propnouns) {
      this.propnounStrings.push(this.originalWordList[i])
    }
  }

  isAInCorrectLocation(word, index) {
    return this.originalWordList[index] === word.trim().toLowerCase()
  }

  getCountOfCorrectWords(words) {
    let correctCount = 0
    for (let i = 0; i < words.length; i++) {
      const correct = this.isAInCorrectLocation(words[i], i)
      correctCount += correct ? 1 : 0
    }

    return correctCount
  }

  getOriginalIndicesForWord(word) {
    const mapped = this.originalWordList.map((w, i) =>
      w.trim().toLowerCase() === word.trim().toLowerCase() ? i : null
    )
    const filtered = mapped.filter((o) => o !== null)
    return filtered
  }

  getIndexForMatchingWord(i) {
    const otherWord = this.originalWordList[i]
    return this.wordList.indexOf(otherWord)
  }

  isProperNoun(word) {
    return this.propnounStrings.includes(word)
  }

  getNextHint() {
    if (this.hints.length > 0) {
      this.shrinkage += 1
      this.hintsUsed += 1
      return this.hints.shift()
    } else {
      throw new Error(`don't pop from empty hints`)
    }
  }

  get hintsLeft() {
    return this.hints.length
  }

  registerGreenMoves(n) {
    this.greenMoves += n
  }

  registerMiss() {
    this.shrinkage += 1
  }

  get correctWords() {
    return this.greenMoves / this.totalPossibleGreenMoves
  }

  get misses() {
    return this.shrinkage
  }
}
