import { writeDoc, logEvent as logEventFirebase } from './firebase'

export const logEvent = (eventObj) => {
  logEventFirebase(eventObj)
}

export const trace = async (tag, obj) => {
  let collectionPath

  switch (tag) {
    case 'puzzle_completed':
      collectionPath = 'puzzle_completed'
      break
    default:
      throw new Error('unrecognized tag')
  }

  const p = { ...obj, timestamp: Date.now() }

  writeDoc(collectionPath, p)
}

export const key = {
  puzzle_completed: 'puzzle_completed',
}

